import React from "react";
import useSockets from "../useSockets";
import { useNiconico } from "react-niconico";

export default function Niconico() {
    const { mensaje } = useSockets();
    const [ref, emitText] = useNiconico({
        displayMillis: 10000,
        lineWidth: 3,
        //lineWidth: 10,
    });
    React.useEffect(() => {
        if (mensaje !== null) emitText(mensaje);
        else emitText("cargado");
    }, [emitText, mensaje]);
    return <div style={{ minHeight: "100vh" }} ref={ref}></div>;
}
