import React, { useState, useEffect } from "react";
import styled from "styled-components";
import {
    Heading,
    TextInput,
    Box,
    Button,
    Card,
    Text,
    CardBody,
    CardFooter,
} from "grommet";

import ReactPlayer from "react-player/lazy";

const Container = styled.div`
    min-height: 100vh;
    min-width: 500px;
    text-align: center;
`;

export default function AddVideos(props) {
    const [likesvideos, setLikesvideos] = useState([]);
    const [resto, setResto] = useState([]);
    const [link, setLink] = useState("");
    const [url, setUrl] = useState("");
    const [err, setErr] = useState("");
    const handleOnChange = (event) => {
        const { value } = event.target;
        setLink(value);
    };
    const to10 = () => {
        if (ReactPlayer.canPlay(link)) {
            setLikesvideos((oldArray) => [...oldArray, link]);
            setLink("");
            setErr("");
        } else {
            setErr("El video no se puede reproducir");
        }
    };
    const palresto = (props) => {
        if (ReactPlayer.canPlay(link)) {
            setResto((oldArray) => [...oldArray, link]);
            setLink("");
            setErr("");
        } else {
            setErr("El video no se puede reproducir");
        }
    };

    const handleRemoveItemLikes = (name) => {
        setLikesvideos(likesvideos.filter((item) => item !== name));
    };
    const handleRemoveItemResto = (name) => {
        setResto(resto.filter((item) => item !== name));
    };
    useEffect(() => {
        setUrl(encodeURIComponent(JSON.stringify({ likesvideos, resto })));
    }, [likesvideos, resto]);

    return (
        <Container>
            <Heading margin="none">Inserta tu video</Heading>
            <Box direction="row" pad="small">
                <TextInput
                    placeholder="https://www.youtube.com/watch?v=dQw4w9WgXcQ"
                    value={link}
                    onChange={handleOnChange}
                />
            </Box>
            <Text color="red">{err}</Text>
            <Box direction="row" pad="medium" gap="small" justify="center">
                <Button onClick={palresto} label="1 BARRA" />
                <Button onClick={to10} label="3 BARRAS" />
            </Box>
            <Box direction="row" pad="medium" gap="medium">
                <Box height="fill" width="medium" gap="small">
                    <Text>Para una barra</Text>
                    {resto.map((video, id) => (
                        <Card
                            key={id}
                            animation="fadeIn"
                            height="fill"
                            width="medium"
                            background="light-2"
                        >
                            <CardBody pad="medium">
                                <ReactPlayer
                                    width="100%"
                                    height="100%"
                                    controls
                                    url={video}
                                />
                            </CardBody>
                            <CardFooter
                                pad={{ horizontal: "small" }}
                                background="light-3"
                            >
                                <Button
                                    hoverIndicator
                                    disabled
                                    label="3 BARRS"
                                />
                                <Button
                                    hoverIndicator
                                    label="ELIMINAR"
                                    onClick={() => handleRemoveItemResto(video)}
                                />
                            </CardFooter>
                        </Card>
                    ))}
                </Box>
                <Box height="fill" width="medium" gap="small">
                    <Text>Para tres barras</Text>

                    {likesvideos.map((video, id) => (
                        <Card
                            key={id}
                            animation="fadeIn"
                            height="fill"
                            width="medium"
                            background="light-1"
                        >
                            <CardBody pad="medium">
                                <ReactPlayer
                                    width="100%"
                                    height="100%"
                                    controls
                                    url={video}
                                />
                            </CardBody>
                            <CardFooter
                                pad={{ horizontal: "small" }}
                                background="light-2"
                            >
                                <Button
                                    hoverIndicator
                                    disabled
                                    label="1 BARRA"
                                />
                                <Button
                                    hoverIndicator
                                    label="ELIMINAR"
                                    onClick={() => handleRemoveItemLikes(video)}
                                />
                            </CardFooter>
                        </Card>
                    ))}
                </Box>
            </Box>
            {likesvideos.length + resto.length > 0 && (
                <Button
                    primary
                    onClick={() => {
                        navigator.clipboard.writeText(
                            props.location.origin + "/video/?url=" + url,
                        );
                    }}
                    label="Click para copiar"
                />
            )}
        </Container>
    );
}
