import React, { useState, useEffect } from "react";
import "./styles.css";
import ReactPlayer from "react-player";
import useSockets from "../useSockets";
import { useLocation } from "@reach/router";
const animation = {
    likes50:
        "https://ynassets.younow.com/gifts/live/10_LIKES/ios_icon_gift_10_LIKES_bar@3x.png?2",
};
export default function Dvd({ videoid }) {
    const { gift } = useSockets();
    const [likesvideos, setLikesvideos] = useState([]);
    const [resto, setResto] = useState([]);
    const [video, setVideo] = useState(true);
    const [query] = useState(useQuery("url"));
    const [actualvideo, setActualvideo] = useState(
        "https://www.youtube.com/watch?v=MSIxlhIPxIQ",
    );
    useEffect(() => {
        setLikesvideos(JSON.parse(query).likesvideos);
        setResto(JSON.parse(query).resto);
    }, [query]);
    useEffect(() => {
        console.log(gift);
        if (gift.url === "") {
            console.log("no hay");
        } else if (gift.url === animation.likes) {
            console.log("50likes");
            let video =
                likesvideos[Math.floor(Math.random() * likesvideos.length)];
            setActualvideo(video);
        } else if (gift.url !== animation.likes) {
            console.log("resto");
            let video = resto[Math.floor(Math.random() * resto.length)];
            setActualvideo(video);
        }
        setVideo(true);
    }, [gift]);
    useEffect(() => {
        console.log(actualvideo);
    }, [actualvideo]);
    const deletevideo = () => {
        setVideo(false);
    };
    return (
        <div>
            {video && (
                <ReactPlayer
                    width={"100vw"}
                    height={"100vh"}
                    onEnded={() => deletevideo()}
                    url={actualvideo}
                    playing
                />
            )}
        </div>
    );
}

//<img src={image} alt="" />
export const useQuery = (queryParam) => {
    const search = new URLSearchParams(useLocation().search);
    return search.get(queryParam);
};
