/* eslint-disable no-useless-escape */
import React from "react";
import styled from "styled-components";

import { Heading, Box, Button } from "grommet";
import { Link } from "@reach/router";

const Container = styled(Box)`
    min-height: 100vh;
    min-width: 500px;
    text-align: center;
`;

function BotonCustom({ link = "", label = "", origin, ...props }) {
    return (
        <Link to={link}>
            <Button
                label={label}
                //size={"large"}
                onClick={() => {
                    navigator.clipboard.writeText(origin + "/" + link);
                }}
                {...props}
            />
        </Link>
    );
}

export default function Main(props) {
    return (
        <Container>
            <Box pad="xlarge" align="center" gap="medium">
                <Heading margin="none">Hola üëãüèº</Heading>
                <BotonCustom
                    link="text"
                    label="TEXTO FLOTANTE (CLICK TO COPY)"
                    origin={props.location.origin}
                />
                <BotonCustom link="confetti" label="CONFETTI" />
                <BotonCustom link="addvideos" label="ALERTAS VIDEO" />
                <BotonCustom link="particleimage" label="EMOTES FLOTANTE" />
                <BotonCustom link="particlegift" label="REGALOS FLOTANTE" />
            </Box>
        </Container>
    );
}
