import React, { useState, useEffect } from "react";
import Particles from "react-particles-js";
import emoji from "react-easy-emoji";
import useSockets from "../useSockets";
function svgEmoji(input) {
    if (input) {
        return emoji(input, {
            baseUrl: "https://twemoji.maxcdn.com/2/svg/",
            ext: ".svg",
            size: "",
        });
    } else {
        return [];
    }
}

export default function Particle(props) {
    const { gift, images } = useSockets();
    const [imagesarray, setImagesarray] = useState([]);
    useEffect(() => {
        console.log(gift);
    }, [gift]);
    useEffect(() => {
        if (props.path === "particleimage") {
            let temp = imagesarray;
            if (images.url) temp.push(images.url);
            if (temp.length > 10) temp.pop();

            let uniquearray = temp.filter(function (value, index, array) {
                return array.indexOf(value) === index;
            });

            setImagesarray(uniquearray);
        }
    }, [images]);
    useEffect(() => {
        if (props.path === "particlegift") {
            let temp = imagesarray;
            if (images.url) temp.push(gift.url);
            if (temp.length > 10) temp.pop();

            let uniquearray = temp.filter(function (value, index, array) {
                return array.indexOf(value) === index;
            });
            setImagesarray(uniquearray);
        }
    }, [gift]);

    useEffect(() => {
        //console.log(imagesarray);
    }, [imagesarray]);
    if (gift.url)
        return (
            <div>
                <Particles
                    params={{
                        particles: {
                            number: {
                                value: 2,
                                density: {
                                    enable: true,
                                    value_area: 300,
                                },
                            },
                            line_linked: {
                                enable: false,
                            },
                            move: {
                                speed: 1,
                                //out_mode: "out",
                            },
                            shape: {
                                type: ["image"],
                                image: [
                                    /*
                                    ...svgEmoji("").map((emote) => {
                                        if (isValidHttpUrl(emote.props.src))
                                            return {
                                                src: emote.props.src,
                                                height: 23,
                                                width: 23,
                                            };
                                        else return "";
                                    }),
                                    */
                                    ...imagesarray.map((imageurl) => {
                                        //console.log(imageurl);
                                        return {
                                            src: imageurl,
                                            height: 23,
                                            width: 23,
                                        };
                                    }),
                                ],
                            },

                            size: {
                                value: 30,
                                random: false,
                                anim: {
                                    enable: true,
                                    speed: 4,
                                    size_min: 10,
                                    sync: false,
                                },
                            },
                        },
                        retina_detect: false,
                    }}
                    style={{ minHeight: "100vh" }}
                />
            </div>
        );
    else return <div></div>;
}
function isValidHttpUrl(string) {
    let url;

    try {
        url = new URL(string);
    } catch (_) {
        return false;
    }

    return url.protocol === "http:" || url.protocol === "https:";
}
